<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">顾问管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/adviser/create' }"> 添加顾问</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="advisers" stripe style="width: 100%">
        <el-table-column label="图片">
          <template slot-scope="scope">
            <img :src="'https://szebra.cn' + (scope.row.avatar && scope.row.avatar.url)" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="name" label="姓名" />
        <el-table-column prop="building_name" label="服务楼盘" show-overflow-tooltip />
        <el-table-column prop="desc" label="职称描述" show-overflow-tooltip />
        <el-table-column prop="level" label="星级" show-overflow-tooltip />
        <el-table-column prop="mobile" label="电话" show-overflow-tooltip />
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small">
              <router-link class='el-button--text' :to="{ path: `/adviser/${scope.row.id}/detail` }">编辑</router-link>
            </el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      currentRow: null,
      dialogType: '',
      dialogMsg: '',
      dialogVisible: false,
      advisers: [],
      curPage: 1,
      pageSize: 20,
      total: 0,
      buildingList: []
    }
  },

  components: {
  },

  methods: {
    // 删除操作
    deleteHandleClick (row) {
      this.$confirm('删除后将清空该顾问信息，确定删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
      .then((r) => {
        this.$http.adviserDelete(row, 'delete').then(res => {
          if (!res) return
          this.$message.success('操作成功')
          this.queryList()
        })
      })
      .catch(() => {})
    },
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.adviserList(params).then((res) => {
        if (!res) return
        let result = res.data.result_rows
        result.advisers.forEach(item1 => {
          this.buildingList.forEach(item2 => {
            if (item2.id == item1.building_id) {
              item1.building_name = item2.name
            }
          })
        })
        this.advisers = result.advisers
        this.total = result.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },
    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    }
  },
  created () {
    let _this = this 
    this.$http.buildingList({ per: 1000, page: 0 }).then(res => {
      if (res.data.retcode ==0) {
        _this.buildingList = res.data.result_rows.buildings
        _this.queryList()
      }
    })
   
  }
}
</script>

